import { createGlobalStyle } from 'styled-components'

const GlobalStyle = createGlobalStyle`
  body {

    img {
      height: auto;
      max-width: 100%;
    }
  }

  ul {
    list-style: none; 
  }

  li {
    display: flex;
    align-items: center;
  }

  li::before {
    content: "•";
    color: ${({ theme }) => theme.colors.primary};
    margin-right: 8px;
  }

  a,button,div {border-radius : 3px !important}

  nav a svg {display: none !important;}

  #swap-button,#confirm-swap-or-send,#join-pool-button{background-color: #0073cf !important}

  ::-webkit-scrollbar-thumb{background: #0073cf !important}

  .hvrsdn .jhEWmv{color: #fff !important}

  .hvrsdn .ccLUVr{color: #fff !important}

  nav button{color: #fff !important; background-color: #0073cf !important}

  nav{height: 50px !important;}

  .sc-brSvTw{margin-top: 45px !important;}

  .sc-cjrPHo .sc-hKwDye{background-color: #0073cf !important}

  .sc-brSvTw,.hXQGJo{margin-left: 0px !important;max-width: 100% !important; background-color: #1c1d20;}

  #import-pool-link{color: #077bd3 !important}

  .sc-fTxOGA button{color: #077bd3 !important;}

  .sc-dwFVFH{background-image: none !important; padding: 15px 15px !important;}
  
`

export default GlobalStyle
